body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #fbf8ef;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 900;
}

.tile {
  background: #d6cdc4;
  width: 90px;
  height: 90px;
  border-radius: 10px;
  color: #776e65;
  text-align: center;
  vertical-align: center;
  user-select: none;
  border: 5px solid #bbada0;

  display: flex;
  justify-content: center;
  align-items: center;
}

.x2 {
  background: #eee4da;
}

.x4 {
  background: #ede0c8;
}

.x8 {
  background: #f2b179;
  color: #f9f6f2;
}

.x16 {
  background: #f59563;
  color: #f9f6f2;
}

.x32 {
  background: #f67c5f;
  color: #f9f6f2;
}

.x64 {
  background: #f65e3b;
  color: #f9f6f2;
}

.x128 {
  background: #edcf72;
  color: #f9f6f2;
}

.x256 {
  background: #edcc61;
  color: #f9f6f2;
}

.x512 {
  background: #edc850;
  color: #f9f6f2;
}

.x1024 {
  background: #edc53f;
  color: #f9f6f2;
}

.x2048 {
  background: #edc22e;
  color: #f9f6f2;
}

#board {
  width: 400px;
  height: 400px;

  background: #bbada0;
  border-radius: 5px;
  border: 6px solid #bbada5;

  display: flex;
  flex-wrap: wrap;
}

.game_header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 24px;
  padding: 10px;
  box-sizing: border-box;
}

h1 {
  background: #edc22e;
  color: #f9f6f2;
  width: 75px;
  height: 75px;
  font-size: 24px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin: 0;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #d6cdc4;
  width: 75px;
  height: 75px;
  border-radius: 5px;
  color: #776e65;
  box-sizing: border-box;
  font-size: 16px;
  margin: 0 8px 0 0;
}

.controls {
  display: flex;
}

.button {
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #f9f6f2;
  font-family: sans-serif;
  font-weight: 700;
  font-size: 16px;
  width: 75px;
  height: 75px;

  transition: 0.25s ease background;
}

.start {
  background: #1dae28;
  font-size: 20px;

  &:hover {
    background: #179921;
  }
}

.restart {
  background: #f1b2b2;

  &:hover {
    background: #f87474;
  }
}

.message {
  box-sizing: border-box;
  width: 100%;
  background: #d6cdc4;
  color: #776e65;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  font-size: 20px;
}

.hidden {
  display: none;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 350px;
}

.message-win {
  background: #edc22e;
  color: #f9f6f2;
}

.message-container {
  width: 100%;
  height: 150px;
}
